import React from "react";
import "../App.css"
import ExperienceColumn from "./ExperienceColumn";
import MySpanComponent from "./SpanComponent";
function Experience() {

    const [index, setIndex] = React.useState(0);
    const experienceColumns = [0, 1, 2, 3, 4];


    return (
        <div id="experience">
            <div className="experience-container">

                <div className="spanContainer">
                    <MySpanComponent value="H" color="black" />
                    <MySpanComponent value="I" color="black" />
                    <MySpanComponent value="S" color="black" />
                    <MySpanComponent value="T" color="black" />
                    <MySpanComponent value="O" color="black" />
                    <MySpanComponent value="R" color="black" />
                    <MySpanComponent value="I" color="black" />
                    <MySpanComponent value="A" color="black" />
                    <span className="space" />
                    <MySpanComponent value="I" color="black" />
                    <span className="space" />
                    <MySpanComponent value="R" color="black" />
                    <MySpanComponent value="O" color="black" />
                    <MySpanComponent value="Z" color="black" />
                    <MySpanComponent value="W" color="black" />
                    <MySpanComponent value="" color="black" />
                    <MySpanComponent value="J" color="black" />
                </div>




                <div className="fd-r flexC bx-s p-5 br-5 expData-container">
                    <div className="experiencePhoto m-5">
                        <img src="./Photos/experience.svg" alt="" />
                    </div>
                    <div className="flexC fd-c m-5 ">
                        <div className="column-holder" >
                            <div className="slideShowSlider flexC fd-r" style={{ transform: `translate3d(${-index * 20}%,0,0)` }}>
                                <ExperienceColumn bottomData="2018" number="01" topic="Pierwsze Kroki" desc="Pocztki nauki HTML/CSS. Przeszukiwanie zasobw internetowych, aby poszerza sw wiedz" />
                                <ExperienceColumn topData="2019" number="02" topic="Rozwj" desc="Krok w animacje CSS. Pocztek korzystania z flexbox/grid layout" />
                                <ExperienceColumn bottomData="2020" number="03" topic="JavaScript" desc="Zagbienie si w jzyk JavaScript. Pisanie pierwszych skryptw, wsppracujcych z grami przegldarkowymi" />
                                <ExperienceColumn topData="2021" number="04" topic="Kursy" desc="Zakoczenie kilku kursw internetowych zwizanych z tematyk tworzenia stron internetowych." />
                                <ExperienceColumn bottomData="2022" number="05" topic="Studia" desc="Rozpoczcie studiw zaocznych na kierunku Informatyka. Pocztek nauki biblioteki React." />
                            </div>
                        </div>
                        <div className="slideshowDots fd-r flexC">
                            {experienceColumns.map((_, idx) => (
                                <div onClick={() => {
                                    setIndex(idx);
                                }} key={idx} className={`slideshowDot${index === idx ? " activeDot" : ""}`}></div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>


        </div>

    )
}

export default Experience;